.contentScrollbar {
	display: flex;
	flex-direction: column;
    height: 716px;
	flex-basis: 30%;
	background-color: #f4f5f7;
	margin: 2px;
	margin-top: 0;
	border-radius: 12px;
    padding: 8px;
}
.itemNew {
	flex-basis: 15%;
    background-color: #fff;
    margin-bottom: 8px;
    border-radius: 12px;
    padding: 10px 15px;
    cursor: pointer;
}

.itemVisited{
    flex-basis: 15%;
    background-color: transparent;
    margin-bottom: 8px;
    border-radius: 12px;
    padding: 10px 15px;
    cursor: pointer;
}

.itemFocus{
    flex-basis: 15%;
    box-shadow:  3px 6px 8px rgba(84, 84, 84, 0.2);
    background-color: #fff;
    margin-bottom: 8px;
    border-radius: 12px;
    padding: 10px 15px;
    cursor: pointer;
}

.newMessage{
    display: inline-block;
    height: 20px;
    width: 39px;
    background-color: #0165FF;
    color: white;
    text-align: center;
    text-transform: uppercase;
    font-size: 10px;
    padding: 5px 0;
    border-radius: 4px;
    font-family: Arial, Helvetica, sans-serif;
    margin-right: 10px;
}

.newMessageVisited{
    display: none;
}


.item__Span{
    color: #6B778C;
    font-size: 10px;
    padding: 0;

}

.item__Title{
    margin: 10px 0;
}
 .newTitle{
    color: #0165FF;
    margin: 10px 0;
}


p{
    font-size: 10px;
    color: #6B778C;

}